
.card{
	background-color: green;
	direction: inherit; 
	padding: 3px;
	margin: 10px;
	display: inline-block;
	grid-row:inherit; 
	transition: all .2s ease-in-out;
}
.card::hover { 
	transform: scale(1.1); 
}


